function Curve() {
    return (
        <div class="curve">
            <svg data-name="Layer 1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1200 120" preserveAspectRatio="none">
                <path d="M600,112.77C268.63,112.77,0,65.52,0,7.23V120H1200V7.23C1200,65.52,931.37,112.77,600,112.77Z" class="shape-fill"></path>
            </svg>
        </div>
    );
}

function Wave() {
    return (
        <div className="wave">
            <svg data-name="Layer 1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1200 120" preserveAspectRatio="none">
                <path d="M985.66,92.83C906.67,72,823.78,31,743.84,14.19c-82.26-17.34-168.06-16.33-250.45.39-57.84,11.73-114,31.07-172,41.86A600.21,600.21,0,0,1,0,27.35V120H1200V95.8C1132.19,118.92,1055.71,111.31,985.66,92.83Z" class="shape-fill"></path>
            </svg>
        </div>
    );
}

function disp(text,font,pad) {
    let canvas = document.createElement("canvas");
    let context=canvas.getContext("2d");
    context.font=font;
    let width=context.measureText(text).width;
    
    var lines=Math.ceil((width+60)/window.innerWidth);
    if(lines<=1) return "100%";
    if(lines==2) return "50%";
    if(lines==3) return "25%";
    else return "15%";
}

function Unitbanner({num,img}) {
    return (
        <div className="textBack">
            <motion.h1 animate={{y:0}}
                initial={{y:"100%"}}
                transition={{delay:0.03, duration:0.3}}
                className="hdr">Unit {num}</motion.h1>
            <motion.p animate={{y:0}}
                initial={{y:disp(unitName[num-1],"41px chakra petch")}}
                transition={{delay:0.03, duration:0.4}}
                className="big">{unitName[num-1]}</motion.p>
            <motion.p animate={{y:0}}
                initial={{y:"100%"}}
                transition={{delay:0.03, duration:0.5}}
                className="small">{unitDesc[num-1]}</motion.p>
        </div>
    );
}

function Subunitbanner({unit,num}) {
    return (
        <div className="textBack">
            {/*
            <motion.h1 animate={{y:0}}
                initial={{y:"100%"}}
                transition={{delay:0.05, duration:0.3}}
                className="hdr">Unit {unit}  {unitName[unit-1]}</motion.h1>
            */}
            <motion.div
                initial={{opacity:0}}
                animate={{opacity:1}}
                transition={{delay:0.2, duration:0.5}}>
                <Link to={unitLinks[unit-1]}>
                    <FontAwesomeIcon icon={faArrowLeft} className="backButton" />
                </Link>
            </motion.div>
            <motion.p animate={{y:0}}
                initial={{y:disp(subUnit[unit-1][num-1],"40px chakra petch")}}
                transition={{delay:0.03, duration:0.4}}
                className="big">{subUnit[unit-1][num-1]}</motion.p>
            {/*
            <motion.p animate={{y:0}}
                initial={{y:"100%"}}
                transition={{delay:0.05, duration:0.5}}
                className="small">{unitDesc[num-1]}</motion.p>
            */}
        </div>
    )
}

// animate?

function Banner({unit,num,img,type}) {
    // Units
    if(type==1) {
        return (
            <div className="unitBack">
                <Unitbanner num={num} img={img} />
            </div>
        );
    }
    // Sub-units
    if(type==6) {
        return (
            <div className="unitBack">
                <Subunitbanner unit={unit} num={num} />
            </div>
        )
    }
    // FRQ
    if(type==5) {
        return (
            <div className="unitBack">
                <div className="textBack">
                    <motion.h1 animate={{y:0}}
                        initial={{y:"100%"}}
                        transition={{delay:0.03,duration:0.3}}
                        className="big">{num} FRQ</motion.h1>
                    <motion.p animate={{y:0}}
                        initial={{y:"100%"}}
                        transition={{delay:0.03, duration:0.4}}
                        className="small">insert description</motion.p>
                </div>
            </div>
        );
    }
    // Home
    if(type==2) {
        return (
            <div className="unitBack">
                <div className="textBack">
                    <motion.h1 animate={{y:0}}
                        initial={{y:disp("Welcome to Chem34","40px chakra petch")}}
                        transition={{delay:0.03,duration:0.3}}
                        className="big">Welcome to Chem34</motion.h1>
                    <motion.p animate={{y:0}}
                        initial={{y:"100%"}}
                        transition={{delay:0.03, duration:0.4}}
                        className="small">insert description</motion.p>
                </div>
            </div>
        );
    }
    // Info
    if(type==3) {
        return (
            <div className="unitBack">
                <div className="textBack">
                    <motion.h1 animate={{y:0}}
                        initial={{y:"100%"}}
                        transition={{delay:0.03,duration:0.3}}
                        className="big">Info to Know</motion.h1>
                    <motion.p animate={{y:0}}
                        initial={{y:"100%"}}
                        transition={{delay:0.03, duration:0.4}}
                        className="small">insert description</motion.p>
                </div>
            </div>
        );
    }
    // About Us
    if(type==4) {
        return (
            <div className="unitBack">
                <div className="textBack">
                    <motion.h1 animate={{y:0}}
                        initial={{y:disp("For Students, By Students.","40px chakra petch")}}
                        transition={{delay:0.03,duration:0.3}}
                        className="big">For Students, By Students.</motion.h1>
                    <motion.p animate={{y:0}}
                        initial={{y:"100%"}}
                        transition={{delay:0.03, duration:0.4}}
                        className="small">Learn about the people who make Chem34 possible.</motion.p>
                </div>
            </div>
        );
    }
}

