import { Link } from 'react-router-dom';
import "./hexgrid.css";

function Hexleft({num}) {
    return (
        <svg viewBox="0 0 185 151" width="236.8px" height="121.6px" preserveAspectRatio="xMidYMid meet">
            <polygon points="50,10 100,50 100,100 50,140 1,100 1,50"
                     stroke="black"
                     fill="black"/>
            <text x="35" y="85" fill="white" fontSize="30px">{num}</text>
            <div className="blank"></div>
        </svg>
    );
}

function Hexright({num}) {
    return (
        <svg viewBox="-84 0 185 151" width="236.8px" height="121.6px" preserveAspectRatio="xMidYMid meet">
            <div className="blank"></div>
            <polygon points="50,10 100,50 100,100 50,140 1,100 1,50"
                     stroke="black"
                     fill="black"/>
            <text x="35" y="85" fill="white" fontSize="30px">{num}</text>
        </svg>
    );
}

// add lines extending from text
function Hexrow({name,left,num,link}) {
    if(left) {
        return (
            <div className="hexBack">
                <Hexleft className="hex node" num={num}/>
                <div className="left both vert-center changeL">
                    <Link to={link} className="title item">
                        {name}
                    </Link>
                </div>
            </div>
        );
    }
    return (
        <div className="hexBack">
            <div className="right both vert-center changeR">
                <Link to={link} className="title item">
                    {name}
                </Link>
            </div>
            <Hexright className="hex node" num={num}/>
        </div>
    );
}

function Hexgrid({ind}) {
    const parts=({name,left,num,link}) => <Hexrow name={name} left={left} num={num} link={link}/>;
    return (
        <div className="gen">
            {unitParts[ind-1].map(parts)}
        </div>
    );
}

