import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faNetworkWired, faBookOpen, faFilePen, faCircleCheck, faNewspaper } from '@fortawesome/free-solid-svg-icons';

import Banner from "../../Content/Banner.js";
import Navbar from "../../Content/Navigation/Navbar.js";

import "./subunitpage.css";

function Subunitpage({unit, num}) {
    return (
        <div>
            <Navbar />
            <Banner unit={unit} num={num} type="6" />
            <div className="sup-cont3">
                <div className="sup-cont2">
                    <div className="sup-cont2">
                        <hr className="sup-divider" />
                        <div className="sup-cont">
                            <text className="sup-title">Connected Nodes</text>
                            <FontAwesomeIcon className="sup-title-icon" icon={faNetworkWired} />
                        </div>
                        {/* list of items from "nodes" */}
                    </div>
                    <div className="sup-cont2">
                        <hr className="sup-divider" />
                        <div className="sup-cont">
                            <text className="sup-title">Readings</text>
                            <FontAwesomeIcon className="sup-title-icon" icon={faBookOpen} />
                        </div>
                        {/* list of items from "nodes" */}
                    </div>
                    <div className="sup-cont2">
                        <hr className="sup-divider" />
                        <div className="sup-cont">
                            <text className="sup-title">Practice Problems</text>
                            <FontAwesomeIcon className="sup-title-icon" icon={faFilePen} />
                        </div>
                        {/* list of items from "nodes" */}
                    </div>
                    <div className="sup-cont2">
                        <hr className="sup-divider" />
                        <div className="sup-cont">
                            <text className="sup-title">Solutions</text>
                            <FontAwesomeIcon className="sup-title-icon" icon={faCircleCheck} />
                        </div>
                        {/* list of items from "nodes" */}
                    </div>
                    <div className="sup-cont2">
                        <hr className="sup-divider" />
                        <div className="sup-cont">
                            <text className="sup-title">Additional Resources</text>
                            <FontAwesomeIcon className="sup-title-icon" icon={faNewspaper} />
                        </div>
                        {/* list of items from "nodes" */}
                    </div>
                </div>
            </div>
        </div>
    )
}

