import Button from '@mui/joy/Button';
import Card from '@mui/joy/Card';
import CardCover from '@mui/joy/CardCover';
import CardContent from '@mui/joy/CardContent';
import CardActions from '@mui/joy/CardActions';
import Typography from '@mui/joy/Typography';

import { Link } from 'react-router-dom';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faMagnifyingGlass, faCircleNodes, faPenToSquare } from '@fortawesome/free-solid-svg-icons';

function FeatureCard({num,image}) {
    return (
        <Card component="li" style={{maxHeight:"90vh"}} sx={{ minWidth: 300, height:500, flexGrow: 1 }}>
            <CardCover>
                <img
                    src={image}
                    srcSet={image}
                    loading="lazy"
                    alt=""
                />
            </CardCover>
            <CardContent>
                <Typography level="h6" fontWeight="lg" textColor="#dadce1" mt={{ xs: 12, sm: 15 }} >
                    {cont[num][0]}
                    <FontAwesomeIcon icon={cont[num][1]} style={{marginLeft:"15px"}} />
                </Typography>
                <Typography textColor="neutral.300">{cont[num][2]}</Typography>
            </CardContent>
            <CardActions buttonFlex="0 1 120px">
                <Link to={cont[num][4]}>
                    <Button variant="solid" color="neutral">
                        {cont[num][3]}
                    </Button>
                </Link>
            </CardActions>
        </Card>
    );
}

const cont=[
    // [name, icon, description, button text, button link]
    ["Search Bar",faMagnifyingGlass,"description","Start",""],
    ["Wiki",faCircleNodes,"description","Unit 1","/unit1"],
    ["FRQ",faPenToSquare,"description","2022 FRQ","/frq2022"]
]

export default FeatureCard;