import * as React from 'react';
import Backdrop from '@mui/material/Backdrop';
import Box from '@mui/material/Box';
import Modal from '@mui/material/Modal';
import Fade from '@mui/material/Fade';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';

var name=[["Kimi Ma", "Aarushi Mehrotra", "Cyriana Lara", "Kallie Wang", "Elisa Xie"],
          ["Andy Zheng", "Bowen Cheng", "William Kim", "Eric Chen"]];
var desc=[["","","","",""],
          ["","","",""]];

const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    p: 4,
}

function Contpop({group,ind}) {
    const [open, setOpen] = React.useState(false);
    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);

    return (
        <div>
            <Button onClick={handleOpen}>{name[group][ind]}</Button>
            <Modal
                aria-labelledby="transition-modal-title"
                aria-describedby="transition-modal-description"
                open={open}
                onClose={handleClose}
                closeAfterTransition
                slots={{ backdrop: Backdrop }}
                slotProps={{
                    backdrop: {
                        timeout: 500,
                    },
                }}
            >
                <Fade in={open}>
                    <Box sx={style}>
                        <Typography id="transition-modal-title" variant="h6" component="h2">
                            {name[group][ind]}
                        </Typography>
                        <Typography id="transition-modal-description" sx={{ mt: 2 }}>
                            {desc[group][ind]}
                        </Typography>
                    </Box>
                </Fade>
            </Modal>
        </div>
    );
}

function Contgrid() {
    return (
        <div>
            <div>
                <Contpop group={0} ind={0} />
                <Contpop group={0} ind={1} />
                <Contpop group={0} ind={2} />
                <Contpop group={0} ind={3} />
                <Contpop group={0} ind={4} />
            </div>
            <div style={{height:"50px"}} />
            <div>
                <Contpop group={1} ind={0} />
                <Contpop group={1} ind={1} />
                <Contpop group={1} ind={2} />
                <Contpop group={1} ind={3} />
            </div>
        </div>
    );
}

export default Contgrid;