import Hexgrid from "./Content/Hexgrid.js";
import Banner from "./Content/Banner.js";
import Navbar from "./Content/Navigation/Navbar.js";

function Unitpage({num}) {
    return (
        <div>
            <Navbar from="1" />
            <Banner num={num} type="1" />
            <Hexgrid ind={num}/>
        </div>
    );
}

export default Unitpage;