import * as React from 'react';
import { styled } from '@mui/material/styles';
import ArrowForwardIosSharpIcon from '@mui/icons-material/ArrowForwardIosSharp';
import MuiAccordion from '@mui/material/Accordion';
import MuiAccordionSummary from '@mui/material/AccordionSummary';
import MuiAccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';
import { rgba } from 'polished';
//import React, {useState} from "react";

import Banner from "../Content/Banner.js";
import Navbar from "../Content/Navigation/Navbar.js";
import Contgrid from "../Content/Contgrid.js";

import "./aboutus.css";

const Accordion = styled((props) => (
    <MuiAccordion disableGutters elevation={0} square {...props} />
  ))(({ theme }) => ({
    border: `1px solid ${theme.palette.divider}`,
    '&:not(:last-child)': {
      borderBottom: 0,
    },
    '&:before': {
      display: 'none',
    },
}));
  
const AccordionSummary = styled((props) => (
    <MuiAccordionSummary
      expandIcon={<ArrowForwardIosSharpIcon sx={{ fontSize: '0.9rem' }} />}
      {...props}
    />
  ))(({ theme }) => ({
    backgroundColor:'#C7CAD1'
      /*theme.palette.mode === 'dark'
        ? 'rgba(255, 255, 255, .05)'
        : 'rgba(0, 0, 0, .03)'*/,
    flexDirection: 'row-reverse',
    '& .MuiAccordionSummary-expandIconWrapper.Mui-expanded': {
      transform: 'rotate(90deg)',
    },
    '& .MuiAccordionSummary-content': {
      marginLeft: theme.spacing(1),
    },
}));
  
const AccordionDetails = styled(MuiAccordionDetails)(({ theme }) => ({
    padding: theme.spacing(2),
    borderTop: '1px solid rgba(0, 0, 0, .125)',
    backgroundColor: '#DDDFE3',
}));


function NewLine(props) {
    const text=props.text;
    const newText=text.split('\n').map(str=><p>{str}</p>);
    return newText;
}


function CustomAccordion({info}) {
    const [expanded, setExpanded] = React.useState('panel1');

    const handleChange = (panel) => (event, newExpanded) => {
        setExpanded(newExpanded ? panel : false);
    };

    return (
        <div>
            <Accordion expanded={expanded === 'panel1'} onChange={handleChange('panel1')}>
                <AccordionSummary aria-controls="panel1d-content" id="panel1d-header">
                    <Typography>{info[0].question}</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <Typography style={{fontSize:"15px"}}>
                        <NewLine text={info[0].answer} />
                    </Typography>
                </AccordionDetails>
            </Accordion>
            <Accordion expanded={expanded === 'panel2'} onChange={handleChange('panel2')}>
                <AccordionSummary aria-controls="panel2d-content" id="panel2d-header">
                    <Typography>{info[1].question}</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <Typography style={{fontSize:"15px"}}>
                        <NewLine text={info[1].answer} />
                    </Typography>
                </AccordionDetails>
            </Accordion>
            <Accordion expanded={expanded === 'panel3'} onChange={handleChange('panel3')}>
                <AccordionSummary aria-controls="panel3d-content" id="panel3d-header">
                    <Typography>{info[2].question}</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <Typography style={{fontSize:"15px"}}>
                        <NewLine text={info[2].answer} />
                    </Typography>
                </AccordionDetails>
            </Accordion>
            <Accordion expanded={expanded === 'panel4'} onChange={handleChange('panel4')}>
                <AccordionSummary aria-controls="panel4d-content" id="panel4d-header">
                    <Typography>{info[3].question}</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <Typography style={{fontSize:"15px"}}>
                        <NewLine text={info[3].answer} />
                    </Typography>
                </AccordionDetails>
            </Accordion>
        </div>
    );
}

function Aboutus() {
    const info=[
        {
            question: "What is Chem 34?",
            answer: "This is a supplementary resource for Payton's AP Chemistry course, designed by students for students. Throughout the school year, all current AP Chemistry students will be put on view, and can use Chem 34 to alleviate confusion and frustration. After the AP Exam, everyone will be allowed to edit and contribute to this Chemistry Wikipedia. Therefore, any flaws (eg. bad explanations, blurry diagrams, etc) in this resource can be corrected by anyone. Chem 34 is designed to be a \"living\" resource that is constantly evolving. Our hope is that this resource never becomes outdated.",
            open: false
        },
        {
            question: "Why is this resource a website?",
            answer: "Textbooks are clunky. They are hard to read, hard to navigate, hard to understand. By designing Chem 34 as a website, it is easier to conceptualize the content and see the interconnectedness of chemistry. We also created \"nodes,\" pages with specific content, allowing for efficient targeting and reinforcing of weaknesses.",
            open: false
        },
        {
            question: "How is the website organized?",
            answer: "Here is how our website is organized. We based our units on the College Board AP Chemistry Units. In each unit, we divided the content into \"nodes,\" or smaller chunks of a unit. On each node, we have Connected Nodes, Readings, Practice Problems, Solutions, and Additional Info. Connected nodes are nodes that relate to current node (eg. \"Valence Electrons and Ionic Compounds\" is related to \"Atomic Structure and Electron Configuration\"). Readings consist of google documents that contain student-written explanations of the topic. Practice Problems are problems (that can be taken from anywhere) that can be used to practice and reinforce the related concept. Solutions are, well, solutions to the Practice Problems. Additional Info consist of external resources that can further help students (a link to a Youtube video, etc). \n\n To avoid any remaining confusion, we will run through an example: \n\n College Board's Unit 3 is \"Intermolecular Forces and Properties\". This unit is then further split into relevant nodes such as \"Ideal Gas Law\". Within the node, then you can see Readings documents explaining the characteristics of gas, precursor of gas laws, applications, etc. Then you will also find the Practice Problems and Solutions as PSET 1 and PSET 1 solutions. \n\n *Note PSET stands for Problem Set.",
            open: false
        },
        {
            question: "What was the inspiration for creating this?",
            answer: "A society grows great when old men plant trees in whose shade they shall never sit.  Greek Proverb \n\n By working on this project, we hope to leave a legacy that outlasts us. If this resource gets continuously used by Payton's Chemistry Department, it can easily last 10 or more years. Chem 34 is a gift to help the future generation, and we sincerely believe that it is a worthy goal. \n\n This website was also heavily inspired by MIT OCW. For curious students, check out MIT's free public courses.",
            open: false
        }
    ]
    return (
        <div>
            <Navbar from={4} />
            <Banner type={4} />
            <div>
                <div className="pad container1">
                    <h3>Why Learn Chemistry?</h3>
                    <div className="cont">
                        <div className="part">
                            <p>"I find that not enough people focus on learning just for the sake of learning itself. Oftentimes, learning is done with a destination in mind--completing homework assignments, maintaining a GPA, passing an AP Exam. This, however, causes you to lose the joy of learning, the beauty of understanding. Why learn chemistry? Because it is fascinating. When taking AP Chemistry, I encourage you to worry less about the school aspect. Instead, take the time and effort to be amazed by the creativity of our universe."</p>
                            <p style={{textAlign:"right"}}> - Bowen Cheng</p>
                            <br />
                            <p>"Have you ever wondered how fireworks are made, why certain explosions occur, and what are the fundamental building blocks of any matter? If so, chemistry is for you. Through chemistry, you can explore various transformations and develop your own potion! (although most likely not drinkable or magical) Learning chemistry will reveal a whole new understanding of how the world works. Without chemistry, we wouldn't understand why the leaves change colors and how to preserve or even cook food. There will always be some chemical reaction affecting us one way or another, and it's waiting for you to explore."</p>
                            <p style={{textAlign:"right"}}> - Andy Zheng</p>
                        </div>
                    </div>
                </div>
                <div className="pad container2">
                    <h3>FAQs</h3>
                    <div className="cont">
                        <div className="part">
                            <CustomAccordion info={info} />
                        </div>
                    </div>
                </div>
                <div className="pad container3">
                    <h3>Contributors</h3>
                    <div className="cont"><Contgrid /></div>
                </div>
            </div>
        </div>
    );
}

export default Aboutus;