import * as React from 'react';
import Box from '@mui/joy/Box';

import Banner from "../Content/Banner.js";
import Navbar from "../Content/Navigation/Navbar.js";

import FeatureCard from "../Content/Card.js";

function Home() {
    return (
        <div>
            <Navbar from={2} />
            <Banner type={2} />
            {/*<h3 style={{ marginTop:"100px",marginBottom:"0px" }}>Features</h3>*/}
            <div style={{ display:"flex",padding:"100px",justifyContent:"center",alignItems:"center" }}>
                <Box
                    component="ul"
                    sx={{ display: 'flex', gap: 5, maxWidth:"1500px", flexWrap: 'wrap', p: 0, m: 0 }}
                    >
                    <FeatureCard num={1} image="https://upload.wikimedia.org/wikipedia/commons/4/49/A_black_image.jpg" />
                    <FeatureCard num={2} image="https://upload.wikimedia.org/wikipedia/commons/4/49/A_black_image.jpg" />
                    <FeatureCard num={0} image="https://upload.wikimedia.org/wikipedia/commons/4/49/A_black_image.jpg" />
                </Box>
            </div>
        </div>
    );
}

export default Home;