import React, { useState, useEffect, useRef, forwardRef } from 'react';
import { Link } from 'react-router-dom';
import { CSSTransition } from 'react-transition-group';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowLeft, faChevronRight, faBars, faHouse } from '@fortawesome/free-solid-svg-icons';
import "./navbar.css";

// npm i react-transition-group

function Navbar() {
    return (
        <Navcon>
            <NavItem to="/home" name="navb-item homeButton" cont={<FontAwesomeIcon icon={faHouse} style={{color: "#dadce1",}} />} />
            <NavDrop name="icon-button bigSee" cont="Units"><UnitDropdown /></NavDrop>
            <NavDrop name="icon-button bigSee" cont="FRQs"><FRQDropdown /></NavDrop>
            {/*<NavItem to="/info" name="navb-item bigSee" cont="Info" />*/}
            <NavItem to="/aboutus" name="navb-item bigSee" cont="About Us" />
            <NavPress name="icon-button smallSee-icon" cont={<FontAwesomeIcon icon={faBars}  className="smallSee-icon" style={{color: "#dadce1",}} />}>
                <FullDropdownMenu />
            </NavPress>
        </Navcon>
    );
};

function Navcon(props) {
    return (
        <nav className="navb">
            <ul className="navb-nav press">{props.children}</ul>
        </nav>
    );
}

function NavItem(props) {
    return (
        <li className={props.name}>
            <Link to={props.to} className="icon-button">
                {props.cont}
            </Link>
        </li>
    );
}

function NavDrop(props) {
    const [open,setOpen]=useState(false);

    function useClickOutside(ref) {
        useEffect(() => {
            function handleClickOutside(event) {
                if(ref.current && !ref.current.contains(event.target)) {
                    setOpen(false);
                }
            }
            document.addEventListener("mousedown",handleClickOutside);
            return () => {
                document.removeEventListener("mousedown",handleClickOutside);
            };
        }, [ref]);
    }
    
    const wrapperRef=useRef(null);
    useClickOutside(wrapperRef);

    return (
        <li className="navb-item" ref={wrapperRef}>
            <a className={props.name} onClick={()=>setOpen(!open)}>
                {props.cont}
            </a>
            {open&&props.children}
        </li>
    );
}

// compressed version of navbar
function NavPress(props) {
    const [open, setOpen]=useState(false);

    function useClickOutside(ref) {
        useEffect(() => {
            function handleClickOutside(event) {
                if(ref.current && !ref.current.contains(event.target)) {
                    setOpen(false);
                }
            }
            document.addEventListener("mousedown",handleClickOutside);
            return () => {
                document.removeEventListener("mousedown",handleClickOutside);
            };
        }, [ref]);
    }
    
    const wrapperRef=useRef(null);
    useClickOutside(wrapperRef);

    return (
        <li className="navb-item smallSee-icon" ref={wrapperRef}>
            <a className={props.name} onClick={()=>setOpen(!open)}>
                {props.cont}
            </a>
            {open && props.children}
        </li>
    );
}

function UnitDropdown() {

    const [activeMenu, setActiveMenu]=useState('main');
    const [menuHeight, setMenuHeight]=useState(null);
    const dropdownRef=useRef(null);

    useEffect(() => {
        setMenuHeight(dropdownRef.current?.firstChild.offsetHeight);
    },[]);
    
    function calcHeight(el) {
        const height=el.offsetHeight;
        setMenuHeight(height);
    }

    function DropdownExit(props) {
        return (
            <a href="#" className="menu-item" onClick={()=>props.goToMenu&&setActiveMenu(props.goToMenu)}>
                <span className="icon-left">{props.leftIcon}</span>
                {props.children}
            </a>
        );
    }

    function DropdownItem(props) {
        return (
            <Link to={props.to} className="menu-item" onClick={()=>props.goToMenu&&setActiveMenu(props.goToMenu)}>
                {props.children}
            </Link>
        );
    }

    return (
        <div className="dropdown normalDrop bigSee rounded" style={{height:menuHeight+30}} ref={dropdownRef}>
            <CSSTransition
                in={activeMenu==='main'}
                unmountOnExit
                timeout={500}
                classNames="menu-primary"
                onEnter={calcHeight}>
                <div className="menu">
                    <DropdownItem to="/unit1">Unit 1</DropdownItem>
                    <DropdownItem to="/unit2">Unit 2</DropdownItem>
                    <DropdownItem to="/unit3">Unit 3</DropdownItem>
                    <DropdownItem to="/unit4">Unit 4</DropdownItem>
                    <DropdownItem to="/unit5">Unit 5</DropdownItem>
                    <DropdownItem to="/unit6">Unit 6</DropdownItem>
                    <DropdownItem to="/unit7">Unit 7</DropdownItem>
                    <DropdownItem to="/unit8">Unit 8</DropdownItem>
                    <DropdownItem to="/unit9">Unit 9</DropdownItem>
                </div>
                </CSSTransition>
        </div>
    )
}

function FRQDropdown() {

    const [activeMenu, setActiveMenu]=useState('main');
    const [menuHeight, setMenuHeight]=useState(null);
    const dropdownRef=useRef(null);

    useEffect(() => {
        setMenuHeight(dropdownRef.current?.firstChild.offsetHeight);
    },[]);
    
    function calcHeight(el) {
        const height=el.offsetHeight;
        setMenuHeight(height);
    }

    function DropdownExit(props) {
        return (
            <a href="#" className="menu-item" onClick={()=>props.goToMenu&&setActiveMenu(props.goToMenu)}>
                <span className="icon-left">{props.leftIcon}</span>
                {props.children}
            </a>
        );
    }

    function DropdownItem(props) {
        return (
            <Link to={props.to} className="menu-item" onClick={()=>props.goToMenu&&setActiveMenu(props.goToMenu)}>
                {props.children}
            </Link>
        );
    }

    return (
        <div className="dropdown normalDrop rounded bigSee" style={{height:menuHeight+30}} ref={dropdownRef}>
            <CSSTransition
                in={activeMenu==='main'}
                unmountOnExit
                timeout={500}
                classNames="menu-primary"
                onEnter={calcHeight}>
                <div className="menu">
                    <DropdownItem to="/frq2022">2022</DropdownItem>
                    <DropdownItem to="/frq2021">2021</DropdownItem>
                    <DropdownItem to="/frq2019">2019</DropdownItem>
                    <DropdownItem to="/frq2018">2018</DropdownItem>
                    <DropdownItem to="/frq2017">2017</DropdownItem>
                    <DropdownItem to="/frq2016">2016</DropdownItem>
                    <DropdownItem to="/frq2015">2015</DropdownItem>
                    <DropdownItem to="/frq2014">2014</DropdownItem>
                </div>
                </CSSTransition>
        </div>
    )
}

function FullDropdownMenu() {

    const [activeMenu, setActiveMenu]=useState('main');
    const [menuHeight,setMenuHeight]=useState(null);
    const dropdownRef=useRef(null);

    useEffect(() => {
        setMenuHeight(dropdownRef.current?.firstChild.offsetHeight)
    },[]);

    function calcHeight(el) {
        const height=el.offsetHeight;
        setMenuHeight(height);
    }

    // add hover animation? https://fontawesome.com/icons/arrow-left?f=classic&s=solid
    function DropdownExit(props) {
        return (
            <a href="#" className="menu-item" onClick={()=>props.goToMenu && setActiveMenu(props.goToMenu)}>
                <span className="icon-left">{props.leftIcon}</span>
                {props.children}
            </a>
        );
    }

    function DropdownItem(props) {
        return (
            <Link to={props.to} className="menu-item" onClick={()=>props.goToMenu && setActiveMenu(props.goToMenu)}>
                {props.children}
                <span className="icon-right">{props.rightIcon}</span>
            </Link>
        );
    }
    return (
        <div className="dropdown smallSee-drop" style={{height:menuHeight+30}} ref={dropdownRef}>
            <CSSTransition
                in={activeMenu==='main'}
                unmountOnExit
                timeout={500}
                classNames="menu-primary"
                onEnter={calcHeight}>
                <div className="menu">
                    <DropdownItem
                        to="#"
                        goToMenu="units"
                        rightIcon={<FontAwesomeIcon icon={faChevronRight} style={{color: "#dadce1",}} />}>
                        Units
                    </DropdownItem>
                    <DropdownItem
                        to="#"
                        goToMenu="frqs"
                        rightIcon={<FontAwesomeIcon icon={faChevronRight} style={{color: "#dadce1",}} />}>
                        FRQs
                    </DropdownItem>
                    {/*<DropdownItem as={Link} to="/info">Info</DropdownItem>*/}
                    <DropdownItem as={Link} to="/aboutus">About Us</DropdownItem>
                </div>
            </CSSTransition>
            <CSSTransition
                in={activeMenu==='units'}
                unmountOnExit
                timeout={500}
                classNames="menu-secondary"
                onEnter={calcHeight}>
                <div className="menu">
                    <DropdownExit
                        goToMenu="main"
                        leftIcon={<FontAwesomeIcon icon={faArrowLeft} style={{color:"#dadce1"}} />}>
                        <h3 className="nav-h3">Units</h3>
                    </DropdownExit>
                    <DropdownItem as={Link} to="/unit1">Unit 1</DropdownItem>
                    <DropdownItem as={Link} to="/unit2">Unit 2</DropdownItem>
                    <DropdownItem as={Link} to="/unit3">Unit 3</DropdownItem>
                    <DropdownItem as={Link} to="/unit4">Unit 4</DropdownItem>
                    <DropdownItem as={Link} to="/unit5">Unit 5</DropdownItem>
                    <DropdownItem as={Link} to="/unit6">Unit 6</DropdownItem>
                    <DropdownItem as={Link} to="/unit7">Unit 7</DropdownItem>
                    <DropdownItem as={Link} to="/unit8">Unit 8</DropdownItem>
                    <DropdownItem as={Link} to="/unit9">Unit 9</DropdownItem>
                </div>
            </CSSTransition>
            <CSSTransition
                in={activeMenu==='frqs'}
                unmountOnExit
                timeout={500}
                classNames="menu-secondary"
                onEnter={calcHeight}>
                <div className="menu">
                    <DropdownExit
                        goToMenu="main"
                        leftIcon={<FontAwesomeIcon icon={faArrowLeft} style={{color:"#dadce1"}} />}>
                        <h3 className="nav-h3">FRQs</h3>
                    </DropdownExit>
                    <DropdownItem as={Link} to="/frq2022">2022</DropdownItem>
                    <DropdownItem as={Link} to="/frq2021">2021</DropdownItem>
                    <DropdownItem as={Link} to="/frq2019">2019</DropdownItem>
                    <DropdownItem as={Link} to="/frq2018">2018</DropdownItem>
                    <DropdownItem as={Link} to="/frq2017">2017</DropdownItem>
                    <DropdownItem as={Link} to="/frq2016">2016</DropdownItem>
                    <DropdownItem as={Link} to="/frq2015">2015</DropdownItem>
                    <DropdownItem as={Link} to="/frq2014">2014</DropdownItem>
                </div>
            </CSSTransition>
        </div>
    );
}

export default Navbar;