import { Link } from "react-router-dom";

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFilePen, faCircleCheck, faArrowUpRightFromSquare } from '@fortawesome/free-solid-svg-icons';

import Button from "@mui/joy/Button";

import Navbar from "./Content/Navigation/Navbar.js";
import Banner from "./Content/Banner.js";

import "./frqpage.css";

function FRQpage({num, link}) {
    return (
        <div>
            <Navbar from={5} />
            <Banner num={names[num]} type={5} />
            <div className="sup-cont3">
                <div className="sup-cont2">
                    <div className="sup-cont2">
                        <hr className="sup-divider" />
                        <div className="sup-cont">
                            <text className="sup-title">FRQ</text>
                            <FontAwesomeIcon className="sup-title-icon" icon={faFilePen} />
                        </div>
                        <div id="frq-space1" />
                        <Button as={Link} to={link} target="_blank" size="md" variant="solid" color="neutral">
                            <FontAwesomeIcon icon={faArrowUpRightFromSquare} />
                            <text style={{marginLeft:"15px"}}>Open in New Tab</text>
                        </Button>
                        <div id="frq-space2" />
                        { /*<iframe id="frq-pdf" src={link} allowfullscreen="" />*/ }
                    </div>
                    <div className="sup-cont2">
                        <hr className="sup-divider" />
                        <div className="sup-cont">
                            <text className="sup-title">Solutions</text>
                            <FontAwesomeIcon className="sup-title-icon" icon={faCircleCheck} />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

var names = ["2014", "2015", "2016", "2017", "2018", "2019", "2021", "2022"];

export default FRQpage;