import React from "react";
import {
    MDBFooter,
    MDBContainer,
} from 'mdb-react-ui-kit';
//import wave3 from "./wave3.svg";
import "./footer.css";
import "../font.css";


function Footer() {
    return (
        <MDBFooter className='text-center relative' color='white' bgColor='dark'>
            {/* 
            <div className="back2">
                <img src={wave3} className="curve2"/>
            </div>
            */}
            <MDBContainer className='p-4' style={{transform:"translateY(5%)", margin:"auto"}}>
                <section className='mb-4'>
                    <h1 className="hdr">
                        Notice an error?
                    </h1>
                    <h1 className="hdr">
                        Have a suggestion?
                    </h1>
                    <a className="smaller">wpcpchem34@gmail.com</a>
                </section>
            </MDBContainer>

            <div className='smaller text-center p-3' style={{ backgroundColor: 'rgba(0, 0, 0, 0.2)' }}>
                 2024 Chem34
            </div>
        </MDBFooter>
    );
}

export default Footer;

